.navDropdownBtn {
  // text-align: center;
  color: white;
  background-color: var(--btn-background);
  font-family: var(--modal-font);
  font-size: 0.7em;
  // text-transform: capitalize;
  font-weight: 600;
  letter-spacing: 0.2rem;
  text-decoration: none;
  border: 1px solid gray;
  padding: 10px 20px;
  margin-bottom: 1rem;
  cursor: pointer;

  p {
    width: 100%;
    // text-align: center;
    margin: 0;
  }

  i {
    margin-left: auto;
    cursor: pointer;
  }
}

.selectContainer {
  display: flex;
  justify-content: space-between; 
}

.countryDropdown {
  margin-top: 0.5rem;
  display: flex;
  flex-direction: column;
  font-size: 0.7rem;
  overflow: hidden;
  transition: height 1s ease;
}

.dropdownItem {
  color: white;
  padding-bottom: 5px;
}

.dropdownItem:hover {
  color: white;
  filter: brightness(1.2);
  transform: scale(1);
}

@media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
  .countryDropdown {
    font-size: 0.5rem;
  }
}

@media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
  .navDropdownBtn {
    font-size: 0.5rem;
  }
}
